export default {
	data() {
		return {
			// 全选 默认全选
			isselectedall: true,
			// scroll position
			scrollposition: 0,
			// 实际项目中 购物车数据可从本地缓存中获取或从服务端获取
			cart: [],
			// 订单商品 购物车里已选择的商品生成订单
			ordercart: [],
			// 购买更多
			buymore: [],
			// 为您推荐的商品
			itemrecommend: [],
			// 合计金额
			totalamount: 0,
			// 购物车商品数量 
			cntitems: 0,
			// 显示空购物车背景
			shownullcart: false
		};
	},
	onLoad: function() {
		// 从缓存或服务端获取到购物车商品 这里示例数据如下：
		let objcart = [{
			'groupkey': '另加10元任意换购>>',
			'items': [{
				'id': 1,
				'name': '网易智造苹果快充数据线网易智造苹果快充数据线',
				'imgsrc': '../../static/logo.png',
				'href': '点击了商品请跳转至商品详情页',
				'attributes': '夜黑1米',
				'quantity': 5,
				'price': 1
			}, {
				'id': 2,
				'name': '网易智造苹果快充数据线网易智造苹果快充数据线',
				'imgsrc': '../../static/logo.png',
				'href': '点击了商品请跳转至商品详情页',
				'attributes': '酒红色5米',
				'quantity': 8,
				'price': 1
			}, {
				'id': 6,
				'name': '网易智造苹果快充数据线网易智造苹果快充数据线',
				'imgsrc': '../../static/logo.png',
				'href': '点击了商品请跳转至商品详情页',
				'attributes': '酒红色5米',
				'quantity': 8,
				'price': 1
			}, {
				'id': 7,
				'name': '网易智造苹果快充数据线网易智造苹果快充数据线',
				'imgsrc': '../../static/logo.png',
				'href': '点击了商品请跳转至商品详情页',
				'attributes': '酒红色5米',
				'quantity': 8,
				'price': 1
			}]
		}, {
			'groupkey': '',
			'items': [{
				'id': 3,
				'name': '网易智造苹果快充数据线网易智造苹果快充数据线',
				'imgsrc': '../../static/logo.png',
				'href': '点击了商品请跳转至商品详情页',
				'attributes': '金属色1米',
				'quantity': 3,
				'price': 1,
				'marketinglabel': ''
			}, {
				'id': 4,
				'name': '网易智造苹果快充数据线网易智造苹果快充数据线',
				'imgsrc': '../../static/logo.png',
				'href': '点击了商品请跳转至商品详情页',
				'attributes': '珠光白1.5米',
				'quantity': 9,
				'price': 1
			}]
		}]

		// 这里示例数据对象存入缓存
		try {
			uni.setStorageSync('cart', objcart);
		} catch (e) {
			// error
		}
		// 从缓存获取
		try {
			const objcart = uni.getStorageSync('cart');
			if (objcart) {
				// 装载this cart
				this.cart = objcart;
			}
		} catch (e) {
			// error
		}

		// console.log(this.cart)
		// 默认勾选购物车所有商品 合计金额 合计数量
		for (let i = 0; i < this.cart.length; i++) {
			for (let k = 0; k < this.cart[i].items.length; k++) {
				// 总金额 
				this.totalamount = this.totalamount + this.cart[i].items[k].price * this.cart[i].items[k].quantity
				// 总数量
				this.cntitems = this.cntitems + this.cart[i].items[k].quantity
			}
		}
		this.totalamount = this.fmamount(this.totalamount)
	},
	// 下拉刷新
	onPullDownRefresh() {
		uni.startPullDownRefresh(this.cart)
	},
	// 停止刷新
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);

	},
	computed: {},
	// 关闭购物车前 存入缓存
	beforeDestroy: function() {
		// 购物车数据更新缓存
		try {
			uni.setStorageSync('cart', this.cart);
		} catch (e) {
			// error
		}
	},
	methods: {
		// scroll x 归位
		scrollhoming() {
			this.scrollposition = this.scrollposition - 1
			// console.log(this.scrollposition)
		},
		// scroll x 归位后重置 scrollposition
		scrollhomed() {
			this.scrollposition = 0
			// console.log(this.scrollposition)
		},
		// 点击全选
		clickitemselectedall() {
			this.isselectedall = !this.isselectedall
			// 全选
			if (this.isselectedall) {
				if (this.cart) {
					for (var i = 0; i < this.cart.length; i++) {
						for (let k = 0; k < this.cart[i].items.length; k++) {
							// 未选的则选
							if (this.cart[i].items[k].id < 0) {
								this.cart[i].items[k].id = -this.cart[i].items[k].id
								// 更新总数量
								this.cntitems = this.cntitems + this.cart[i].items[k].quantity
								// 更新总金额
								this.totalamount = this.totalamount + this.cart[i].items[k].price * this.cart[i].items[k].quantity
								this.totalamount = this.fmamount(this.totalamount)
							}
						}
					}
				}
			} else {
				// 全不选
				// this.cntitems = 0
				if (this.cart) {
					for (var i = 0; i < this.cart.length; i++) {
						for (let k = 0; k < this.cart[i].items.length; k++) {
							// 累计总金额和总数量 勾选时加
							if (this.isselected(this.cart[i].items[k].id)) {
								this.cart[i].items[k].id = -this.cart[i].items[k].id
								// 更新总数量
								this.cntitems = this.cntitems - this.cart[i].items[k].quantity
								// 更新总金额
								this.totalamount = this.totalamount - this.cart[i].items[k].price * this.cart[i].items[k].quantity
								this.totalamount = this.fmamount(this.totalamount)
							}
						}
					}
				}
			}
		},
		// 点击勾选
		clickitemselected(id) {
			if (this.cart) {
				for (var i = 0; i < this.cart.length; i++) {
					for (let k = 0; k < this.cart[i].items.length; k++) {
						if (this.cart[i].items[k].id == id) {
							this.cart[i].items[k].id = -this.cart[i].items[k].id
							// 累计总金额和总数量 勾选时加
							if (this.isselected(this.cart[i].items[k].id)) {
								// 更新总数量
								this.cntitems = this.cntitems + this.cart[i].items[k].quantity
								// 更新总金额
								this.totalamount = this.totalamount + this.cart[i].items[k].price * this.cart[i].items[k].quantity
								this.totalamount = this.fmamount(this.totalamount)
								// 最后已勾选则 全选
								if (this._isselectedall()) {
									this.isselectedall = true
								}
							} else {
								// 取消勾选时减
								// 更新总数量
								this.cntitems = this.cntitems - this.cart[i].items[k].quantity
								// 更新总金额
								this.totalamount = this.totalamount - this.cart[i].items[k].price * this.cart[i].items[k].quantity
								this.totalamount = this.fmamount(this.totalamount)
								this.isselectedall = false
							}
							return
						}
					}
				}
			}
		},
		// 点击删除
		clickdel(itemid) {
			for (let i = 0; i < this.cart.length; i++) {
				for (let k = 0; k < this.cart[i].items.length; k++) {
					if (this.cart[i].items[k].id == itemid) {
						// 勾选状态下更新数量和金额
						if (this.isselected(this.cart[i].items[k].id)) {
							// 更新总数量
							this.cntitems = this.cntitems - this.cart[i].items[k].quantity
							// 更新总金额
							this.totalamount = this.totalamount - this.cart[i].items[k].price * this.cart[i].items[k].quantity
							this.totalamount = this.fmamount(this.totalamount)
							// 删除商品
							this.cart[i].items[k].id = -99 - itemid
						} else {
							// 未勾选时 删除商品 列表中不再显示 id= -99-原始id
							this.cart[i].items[k].id = -99 + itemid
						}
						// 如果全部删除 则显示空购物车背景
						if (this._isdeledall()) {
							this.shownullcart = true
							this.isselectedall = false
						}
						return
					}
				}
			}
			// 删除购物车商品时 更新合计金额

		},
		// 减数量
		minusitem(itemid) {
			for (let i = 0; i < this.cart.length; i++) {
				for (let k = 0; k < this.cart[i].items.length; k++) {
					if ((this.cart[i].items[k].id == itemid) && (this.cart[i].items[k].quantity > 0)) {
						// 更新item数量
						this.cart[i].items[k].quantity = this.cart[i].items[k].quantity - 1
						// 勾选状态下更新数量和金额
						if (this.isselected(this.cart[i].items[k].id)) {
							// 更新总数量
							this.updatecntitems(-1)
							// 更新总金额
							this.updatetotalamt(-this.cart[i].items[k].price)
						}
						// 数量减为0时 不勾选
						if (this.cart[i].items[k].quantity == 0) {
							this.cart[i].items[k].id = this._unselected(this.cart[i].items[k].id)
						}
						return
					}
				}
			}
		},
		// 加数量
		plusitem(itemid) {
			for (let i = 0; i < this.cart.length; i++) {
				for (let k = 0; k < this.cart[i].items.length; k++) {
					// 这里需要进行超卖控制 商品可售卖的数量 这里面示例可售卖100
					if ((this.cart[i].items[k].id == itemid) && (this.cart[i].items[k].quantity < 100)) {
						// 更新item数量
						this.cart[i].items[k].quantity = this.cart[i].items[k].quantity + 1
						// 勾选状态下更新数量和金额
						if (this.isselected(this.cart[i].items[k].id)) {
							// 更新总数量
							this.updatecntitems(1)
							// 更新总金额
							this.updatetotalamt(this.cart[i].items[k].price)
						} else {
							// 加数量时未勾选则 勾选
							this.cart[i].items[k].id = this._selected(this.cart[i].items[k].id)
							// 更新总数量、
							this.cntitems = this.cntitems + this.cart[i].items[k].quantity
							// 更新总金额
							this.totalamount = this.totalamount + this.cart[i].items[k].price * this.cart[i].items[k].quantity
							this.totalamount = this.fmamount(this.totalamount)
						}

						// 最后已勾选则 全选
						if (this._isselectedall()) {
							this.isselectedall = true
						}
						return
					}
				}
			}
		},
		// 点击商品href
		clickitemhref(str) {
			uni.showModal({
				content: '点击了商品链接' + str
			})
		},
		// 点击了营销活动
		clickgroupkey(str) {
			let mehref = this.getgroupkeyhref(str)
			uni.showModal({
				content: '点击了营销活动' + mehref
			})
		},
		// 更新合计金额
		updatetotalamt(amt) {
			this.totalamount = this.totalamount + amt
			this.totalamount = this.fmamount(this.totalamount)

		},
		// 更新合计数量
		updatecntitems(cnt) {
			this.cntitems = this.cntitems + cnt
		},
		// 格式化金额
		fmamount(amt) {
			return Math.round(amt * 100) / 100
		},
		// 不勾选
		_unselected(id) {
			return -Math.abs(id)
		},
		// 勾选
		_selected(id) {
			return Math.abs(id)
		},
		// 是否勾选
		isselected(id) {
			return id > 0
		},
		// 是否全部已勾选
		_isselectedall() {
			for (let i = 0; i < this.cart.length; i++) {
				for (let k = 0; k < this.cart[i].items.length; k++) {
					// 存在一个未勾选 则未全选
					if (this.cart[i].items[k].id < 0) {
						return false
					}
				}
			}
			return true
		},
		// 是否全部删除
		_isdeledall() {
			for (let i = 0; i < this.cart.length; i++) {
				for (let k = 0; k < this.cart[i].items.length; k++) {
					// 存在 未删除
					if (this.cart[i].items[k].id > -99) {
						return false
					}
				}
			}
			return true
		},
		// 获取营销页面链接
		getgroupkeyhref(str) {
			// 这里通过营销活动名称 获取营销活动页面 需要自己实现
			return '营销活动页面'
		},
		// 生成订单
		createorder() {
			// 合计金额大于0 创建订单
			if (this.totalamount == 0) {
				uni.showModal({
					content: '请选择下单的商品！'
				})
			} else {
				// 1、处理购物车内已选择的订单生成商品
				for (let i = 0; i < this.cart.length; i++) {
					for (let k = 0; k < this.cart[i].items.length; k++) {
						// item id 大于0 的是勾选的
						if (this.cart[i].items > 0) {
							//这里的item 是下单的
						}
					}
				}
				// 2、生成订单成功后 删除购物车内已生成订单的商品
			}
		}
	}
}
